//
import config from '../config';
const utils = {};


utils.getFetch = (url) => {
	//dataget
	return fetch(config.local+url,{//
		mode:"cors",
		credentials:'include',
		method:'GET',
	});
}


utils.postFetch = (url,data) => {
	data = JSON.stringify(data);
	return fetch(config.local+url,{//
		mode:"cors",
		credentials:'include',
		method:'POST',
		headers:{
			'Content-Type':'application/json;charset=utf-8'
		},
		body:data
	});
}

export default utils;


